/**
 * Divider - 
 * 
 * 
 * 
 */
import React from 'react';

interface DividerProps {
    /**  */
    orientation?: 'horizontal' | 'vertical';
    /**  (1-100 100) */
    length?: number;
    /**  className */
    className?: string;
    /**  */
    responsive?: boolean;
    /**  () */
    spacing?: 'none' | 'sm' | 'md' | 'lg';
}

const spacingClasses = {
    none: '',
    sm: 'my-2',
    md: 'my-4',
    lg: 'my-6',
};

/**
 * 
 *  border 
 * 
 * @example
 * // 30% 
 * <Divider orientation="horizontal" length={30} />
 * 
 * // 50% 
 * <Divider orientation="vertical" length={50} />
 * 
 * // 
 * <Divider responsive length={30} />
 */
export const Divider: React.FC<DividerProps> = ({
    orientation = 'horizontal',
    length = 100,
    className = '',
    responsive = false,
    spacing = 'none',
}) => {
    const spacingClass = spacingClasses[spacing];

    // 
    if (responsive) {
        return (
            <>
                {/*  -  border-l */}
                <div
                    className={`hidden lg:block absolute left-0 top-1/2 -translate-y-1/2 border-l border-border/30 ${className}`}
                    style={{ height: `${length}%` }}
                />
                {/*  -  border-t */}
                <div
                    className={`lg:hidden border-t border-border/30 mx-auto ${spacingClass} ${className}`}
                    style={{ width: `${length}%` }}
                />
            </>
        );
    }

    //  - 
    if (orientation === 'vertical') {
        return (
            <div
                className={`border-l border-border/30 mx-auto ${className}`}
                style={{ height: `${length}%` }}
            />
        );
    }

    //  -  border-t
    return (
        <div
            className={`border-t border-border/30 ${spacingClass} ${className}`}
            style={{ width: `${length}%` }}
        />
    );
};

export default Divider;
