/**
 * ModelLog - 
 * 
 *  LLM 
 */
import React, { useState, useEffect } from 'react';
import { Send, Bot, Clock, Zap, AlertCircle, CheckCircle, Loader2, Trash2, ChevronDown, ChevronRight } from 'lucide-react';
import { ModelLogger, ModelLogEntry } from "@/lib/logger/ModelLogger";

/**  */
const TYPE_LABELS: Record<ModelLogEntry['type'], { label: string; color: string }> = {
    summarize: { label: '', color: 'bg-blue-500/20 text-blue-400' },
    trim: { label: '', color: 'bg-yellow-500/20 text-yellow-500' },
    vectorize: { label: '', color: 'bg-purple-500/20 text-purple-400' },
    query: { label: '', color: 'bg-green-500/20 text-green-400' },
    other: { label: '', color: 'bg-gray-500/20 text-gray-400' },
};

/**  */
const StatusIcon: React.FC<{ status: ModelLogEntry['status'] }> = ({ status }) => {
    switch (status) {
        case 'pending':
            return <Loader2 size={14} className="animate-spin text-yellow-400" />;
        case 'success':
            return <CheckCircle size={14} className="text-green-400" />;
        case 'error':
            return <AlertCircle size={14} className="text-red-400" />;
    }
};

/**  */
const formatTime = (timestamp: number): string => {
    return new Date(timestamp).toLocaleTimeString('zh-CN', {
        hour12: false,
        hour: '2-digit',
        minute: '2-digit',
        second: '2-digit',
    });
};

/**  */
const formatDuration = (ms?: number): string => {
    if (ms === undefined) return '-';
    if (ms < 1000) return `${ms}ms`;
    return `${(ms / 1000).toFixed(1)}s`;
};

/**  */
const LogCard: React.FC<{
    sent: ModelLogEntry;
    received?: ModelLogEntry;
}> = ({ sent, received }) => {
    const [expanded, setExpanded] = useState(false);

    const typeConfig = TYPE_LABELS[sent.type];

    return (
        <div className="border border-border rounded-lg overflow-hidden bg-card">
            {/*  */}
            <div
                className="flex items-center gap-2 px-3 py-2 bg-muted-20 cursor-pointer hover:bg-muted-30"
                onClick={() => setExpanded(!expanded)}
            >
                {expanded ? <ChevronDown size={14} /> : <ChevronRight size={14} />}
                <span className={`px-2 py-0.5 rounded text-xs font-medium ${typeConfig.color}`}>
                    {typeConfig.label}
                </span>

                {/* Model Badge */}
                {sent.model && (
                    <span className="px-1.5 py-0.5 rounded text-[10px] font-medium bg-indigo-500/10 text-indigo-400 border border-indigo-500/20 truncate max-w-[100px]" title={`: ${sent.model}`}>
                        {sent.model}
                    </span>
                )}

                {/* Character Badge */}
                {sent.character && (
                    <span className="px-1.5 py-0.5 rounded text-[10px] font-medium bg-orange-500/10 text-orange-400 border border-orange-500/20 truncate max-w-[80px]" title={`: ${sent.character}`}>
                        {sent.character}
                    </span>
                )}
                <span className="text-xs text-muted-foreground">{formatTime(sent.timestamp)}</span>
                <StatusIcon status={received?.status || sent.status} />
                {sent.floorRange && (
                    <span className="text-xs text-muted-foreground">
                         #{sent.floorRange[0]}-{sent.floorRange[1]}
                    </span>
                )}
                <span className="ml-auto text-xs text-muted-foreground flex items-center gap-1">
                    <Clock size={12} />
                    {formatDuration(received?.duration || sent.duration)}
                </span>
            </div>

            {/*  */}
            {expanded && (
                <div className="flex flex-col md:flex-row">
                    {/*  */}
                    <div className="flex-1 border-r border-border p-3">
                        <div className="flex items-center gap-2 mb-2 text-sm font-medium text-blue-400">
                            <Send size={14} />
                            
                            {sent.tokensSent && (
                                <span className="text-xs text-muted-foreground ml-auto">
                                    ~{sent.tokensSent} tokens
                                </span>
                            )}
                        </div>

                        {sent.systemPrompt && (
                            <div className="mb-3">
                                <div className="text-xs text-muted-foreground mb-1">System</div>
                                <div className="text-sm p-2 bg-muted-20 rounded max-h-32 overflow-y-auto whitespace-pre-wrap">
                                    {sent.systemPrompt}
                                </div>
                            </div>
                        )}

                        {sent.userPrompt && (
                            <div>
                                <div className="text-xs text-muted-foreground mb-1">User</div>
                                <div className="text-sm p-2 bg-muted-20 rounded max-h-48 overflow-y-auto whitespace-pre-wrap">
                                    {sent.userPrompt}
                                </div>
                            </div>
                        )}
                    </div>

                    {/*  */}
                    <div className="flex-1 p-3">
                        <div className="flex items-center gap-2 mb-2 text-sm font-medium text-green-400">
                            <Bot size={14} />
                            
                            {received?.tokensReceived && (
                                <span className="text-xs text-muted-foreground ml-auto">
                                    ~{received.tokensReceived} tokens
                                </span>
                            )}
                        </div>

                        {received?.status === 'error' && received.error && (
                            <div className="p-2 bg-red-500/10 border border-red-500/30 rounded text-sm text-red-400">
                                {received.error}
                            </div>
                        )}

                        {received?.response && (
                            <div className="text-sm p-2 bg-muted-20 rounded max-h-48 overflow-y-auto whitespace-pre-wrap">
                                {received.response}
                            </div>
                        )}

                        {!received && sent.status === 'pending' && (
                            <div className="flex items-center gap-2 text-sm text-muted-foreground">
                                <Loader2 size={14} className="animate-spin" />
                                ...
                            </div>
                        )}
                    </div>
                </div>
            )}
        </div>
    );
};

/** ModelLog  */
export const ModelLog: React.FC = () => {
    const [logs, setLogs] = useState(ModelLogger.getPaired());

    useEffect(() => {
        const unsubscribe = ModelLogger.subscribe(() => {
            setLogs(ModelLogger.getPaired());
        });
        return unsubscribe;
    }, []);

    return (
        <div className="flex flex-col h-full">
            {/*  */}
            <div className="flex items-center justify-between px-4 py-3 border-b border-border shrink-0">
                <div className="flex items-center gap-2">
                    <Zap size={16} className="text-primary" />
                    <span className="font-medium text-foreground"></span>
                    <span className="text-xs text-muted-foreground">({logs.length})</span>
                </div>
                <button
                    className="p-1.5 rounded-md hover:bg-destructive hover:text-destructive-foreground transition-colors text-muted-foreground"
                    onClick={() => ModelLogger.clear()}
                    title=""
                >
                    <Trash2 size={14} />
                </button>
            </div>

            {/*  */}
            <div className="flex-1 overflow-y-auto p-4">
                {logs.length === 0 ? (
                    <div className="flex flex-col items-center justify-center h-full text-muted-foreground gap-3">
                        <Bot size={48} className="opacity-30" />
                        <p className="text-sm"></p>
                        <p className="text-xs"></p>
                    </div>
                ) : (
                    <div className="flex flex-col gap-3">
                        {logs.map(({ sent, received }) => (
                            <LogCard key={sent.id} sent={sent} received={received} />
                        ))}
                    </div>
                )}
            </div>
        </div>
    );
};

export default ModelLog;
