/**
 * TextProcessor - 
 * 
 *  LLM 
 */

/**  */
interface TrimRule {
    pattern: RegExp;
    replacement: string;
    description: string;
}

/**  */
const DEFAULT_TRIM_RULES: TrimRule[] = [
    // 
    { pattern: /\n{3,}/g, replacement: '\n\n', description: '' },
    // 
    { pattern: /^[ \t]+|[ \t]+$/gm, replacement: '', description: '' },
    //  Markdown 
    { pattern: /```\w*\n?/g, replacement: '', description: 'Markdown' },
    // 
    { pattern: /[""]/g, replacement: '"', description: '' },
    { pattern: /['']/g, replacement: "'", description: '' },
    //  LLM 
    { pattern: /^(|||).{0,20}[:]\s*/gm, replacement: '', description: 'LLM' },
    // 
    { pattern: /\n*.{0,50}.{0,30}[]?\s*$/g, replacement: '', description: '' },
];

/**
 * TextProcessor 
 * 
 */
export class TextProcessor {
    private rules: TrimRule[];

    constructor(customRules?: TrimRule[]) {
        this.rules = customRules || DEFAULT_TRIM_RULES;
    }

    /**
     *  LLM 
     * @param text 
     * @returns 
     */
    clean(text: string): string {
        let result = text;

        for (const rule of this.rules) {
            result = result.replace(rule.pattern, rule.replacement);
        }

        return result.trim();
    }

    /**
     * 
     * @param summary 
     * @param metadata 
     */
    formatAsWorldEntry(
        summary: string,
        metadata: {
            floorRange: [number, number];
            timestamp: number;
            characterName?: string;
        }
    ): string {
        const date = new Date(metadata.timestamp);
        const dateStr = date.toLocaleDateString('zh-CN');
        const floorStr = `${metadata.floorRange[0]}-${metadata.floorRange[1]}`;

        //  Token
        let entry = `  [${floorStr}]\n`;
        entry += summary;

        return entry;
    }

    /**
     * 
     * @param text 
     */
    extractPlainText(text: string): string {
        return text
            .replace(/```[\s\S]*?```/g, '') // 
            .replace(/\[([^\]]+)\]\([^)]+\)/g, '$1') // 
            .replace(/[*_~`#]/g, '') //  Markdown 
            .replace(/\n{2,}/g, '\n')
            .trim();
    }

    /**
     * 
     * @param text 
     * @param maxLength 
     * @param suffix 
     */
    truncate(text: string, maxLength: number, suffix = '...'): string {
        if (text.length <= maxLength) {
            return text;
        }
        return text.slice(0, maxLength - suffix.length) + suffix;
    }

    /**
     * 
     */
    addRule(rule: TrimRule): void {
        this.rules.push(rule);
    }

    /**
     * 
     */
    getRules(): TrimRule[] {
        return [...this.rules];
    }
}

/**  */
export const textProcessor = new TextProcessor();

export default TextProcessor;
