/**
 * Summarizer 
 */

/**  */
export type TriggerMode = 'auto' | 'manual';

/**  */
export type WorldbookBindMode = 'chat' | 'character';

/** Summarizer  */
export interface SummarizerConfig {
    /**  */
    enabled: boolean;
    /** / */
    triggerMode: TriggerMode;
    /**  N  */
    floorInterval: number;
    /**  */
    worldbookMode: WorldbookBindMode;
    /**  */
    previewEnabled: boolean;
    /**  ID */
    promptTemplateId: string | null;
    /**  LLM  IDnull  */
    llmPresetId: string | null;
    /**  */
    bufferSize: number;
    /**  */
    autoHide: boolean;
}

/**  */
export const DEFAULT_SUMMARIZER_CONFIG: SummarizerConfig = {
    enabled: true,
    triggerMode: 'auto',
    floorInterval: 10,
    worldbookMode: 'chat',
    previewEnabled: true,
    promptTemplateId: null, // 
    llmPresetId: null,      // 
    bufferSize: 3,          //  3 
    autoHide: false,        // 
};

/**  */
export interface SummaryResult {
    /**  */
    content: string;
    /** Token  */
    tokenCount: number;
    /**  [, ] */
    sourceFloors: [number, number];
    /**  */
    timestamp: number;
    /**  */
    writtenToWorldbook: boolean;
    /**  ID */
    worldbookEntryId?: string;
}

/** Summarizer  */
export interface SummarizerStatus {
    /**  */
    running: boolean;
    /**  */
    currentFloor: number;
    /**  */
    lastSummarizedFloor: number;
    /**  */
    pendingFloors: number;
    /**  */
    historyCount: number;
    /**  */
    isSummarizing: boolean;
}

/**  */
export interface SummarizeRequest {
    /**  */
    messages: Array<{
        role: 'user' | 'assistant' | 'system';
        content: string;
        name?: string;
    }>;
    /**  */
    floorRange: [number, number];
    /**  ID */
    templateId?: string;
}

/** LLM  */
export interface LLMRequest {
    /**  */
    systemPrompt: string;
    /**  */
    userPrompt: string;
    /**  ID */
    presetId?: string;
}

/** LLM  */
export interface LLMResponse {
    /**  */
    content: string;
    /**  */
    success: boolean;
    /**  */
    error?: string;
    /** Token  */
    tokenUsage?: {
        prompt: number;
        completion: number;
        total: number;
    };
}
