import React from 'react';
import ReactDOM from 'react-dom/client';
import './styles/main.css';
import App from './App';
import { initializeEngram, setReactRenderer, setGlobalRenderer } from '@/tavern/bridge';
import { RevisionModal } from '@/components/ui/Modal';

//  React 
setReactRenderer((container: HTMLElement, onClose: () => void) => {
    const root = ReactDOM.createRoot(container);
    root.render(React.createElement(App, { onClose }));
    return root;
});

//  (RevisionModal etc)
setGlobalRenderer((container: HTMLElement) => {
    const root = ReactDOM.createRoot(container);
    root.render(
        <div className="pointer-events-auto">
            <RevisionModal />
        </div>
    );
    return root;
});

//  DOM 
if (document.readyState === 'loading') {
    document.addEventListener('DOMContentLoaded', initializeEngram);
} else {
    initializeEngram();
}

// Engram initialization handled in STBridge

