/**
 * LLMAdapter - LLM 
 * 
 *  SillyTavern/TavernHelper LLM API 
 */

import type { LLMRequest, LLMResponse } from './types';

/**
 *  TavernHelper API
 */
function getTavernHelper(): {
    generate?: (options: unknown) => Promise<string>;
    generateRaw?: (options: unknown) => Promise<string>;
} | null {
    try {
        // @ts-expect-error - TavernHelper 
        return window.TavernHelper || null;
    } catch {
        return null;
    }
}

/**
 * LLMAdapter 
 *  LLM 
 */
export class LLMAdapter {
    /**
     *  LLM 
     * @param request 
     */
    async generate(request: LLMRequest): Promise<LLMResponse> {
        const helper = getTavernHelper();

        if (!helper?.generateRaw && !helper?.generate) {
            return {
                success: false,
                content: '',
                error: 'TavernHelper ',
            };
        }

        try {
            let content: string;

            if (helper.generateRaw) {
                //  generateRaw 
                content = await helper.generateRaw({
                    ordered_prompts: [
                        { role: 'system', content: request.systemPrompt },
                        { role: 'user', content: request.userPrompt },
                    ],
                    //  ID
                    // custom_api: request.presetId ? await this.getPresetConfig(request.presetId) : undefined,
                });
            } else if (helper.generate) {
                // fallback:  generate
                content = await helper.generate({
                    user_input: request.userPrompt,
                    system_prompt: request.systemPrompt,
                    should_stream: false,
                    max_chat_history: 0,
                });
            } else {
                throw new Error(' API');
            }

            return {
                success: true,
                content: content || '',
            };
        } catch (e) {
            const errorMsg = e instanceof Error ? e.message : String(e);
            console.error('[Engram] LLMAdapter :', e);

            return {
                success: false,
                content: '',
                error: errorMsg,
            };
        }
    }

    /**
     *  LLM API 
     */
    isAvailable(): boolean {
        const helper = getTavernHelper();
        return !!(helper?.generate || helper?.generateRaw);
    }

    /**
     *  Token 
     * @param text 
     */
    estimateTokens(text: string): number {
        //  2 /token 4 /token
        //  3
        return Math.ceil(text.length / 3);
    }
}

/**  */
export const llmAdapter = new LLMAdapter();

export default LLMAdapter;
